.cart-items {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: scroll;
}

.total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
    color: #444444;
}

.actions {
    text-align: right;
}

.actions button {
    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 2px solid lightsteelblue;
    padding: 0.5rem 2rem;
    /* border-radius: 25px; */
    margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
    background-color: lightslategray;
    border-color: lightslategray;
    color: white;
}

.actions .button--alt {
    color: lightsteelblue;
}

.actions .button {
    background-color: lightslategray;
    color: white;
}
